import { useState } from "react";
import { useHistory } from "react-router-dom";

import classes from "./ButtonLink.module.css";

function ButtonLink(props) {
  const { idx, reduceOther, numReduced, path, title, subTitle } = props;
  const history = useHistory();

  const [clicked, setClicked] = useState(false);

  const handleTransitionEnd = () => {
    if (clicked) {
      history.push(path);
    }
  };

  console.log("numReduced", numReduced);

  return (
    <div
      className={`${classes.cardLinkBox} ${clicked ? classes.fullscreen : ""} ${
        numReduced && idx != numReduced ? classes.reduceNoFullscreen : ""
      }`}
      onTransitionEnd={handleTransitionEnd}
      onClick={() => {
        reduceOther(idx);
        setClicked(true);
      }}
    >
      <div className={classes.linkTextContext}>
        <h4 className={classes.linkTitle}>{title}</h4>
        <p className={classes.linkSubTitle}>{subTitle}</p>
      </div>
    </div>
  );
}

export default ButtonLink;
